<template>
  <div class="content">
    <p>索引</p>
    <cell v-for="(item, idx) in cell" :key="item.content" :target="item.target" :icon-pic="item.pic"
          :text-content="item.content"
          :link="item.link"
          :first="idx===0"></cell>
  </div>
</template>

<script>
import Cell from 'tinny-vue-ui/src/Cell'
export default {
  components: {
    Cell: Cell
  },
  data () {
    return {
      first: true,
      cell: [
        {
          link: '../demo/inputs.html',
          pic: 'icon-inputs',
          content: '输入框组件',
          target: '_blank'
        },
        {
          link: '../demo/cell.html',
          pic: 'icon-cell',
          content: 'cell组件',
          target: '_blank'
        },
        {
          link: '../demo/loading.html',
          pic: 'icon-loading',
          content: 'loading组件',
          target: '_blank'
        },
        {
          link: '../demo/popups.html',
          pic: 'icon-popups',
          content: '弹窗组件',
          target: '_blank'
        },
        {
          link: '../demo/tabbar.html',
          pic: 'icon-tabbar',
          content: 'tabbar组件',
          target: '_blank'
        },
        {
          link: '../demo/toast.html',
          pic: 'icon-toast',
          content: 'toast组件',
          target: '_blank'
        },
        {
          link: '../demo/buttons.html',
          pic: 'icon-buttons',
          content: 'button组件',
          target: '_blank'
        },
        {
          link: '../demo/page.html',
          pic: 'icon-page',
          content: 'page分页组件',
          target: '_blank'
        },
        {
          link: '../demo/switchs.html',
          pic: 'icon-switchs',
          content: 'switch开关',
          target: '_blank'
        },
        {
          link: '../demo/folder.html',
          pic: 'icon-folder',
          content: 'folder折叠面板',
          target: '_blank'
        },
        {
          link: '../demo/progressBar.html',
          pic: 'icon-progress',
          content: 'progressBar进度条组件',
          target: '_blank'
        },
        {
          link: '../demo/inputNum.html',
          pic: 'icon-inputNum',
          content: '加减计数器',
          target: '_blank'
        },
        {
          link: '../demo/stepbar.html',
          pic: 'icon-step',
          content: '步骤条',
          target: '_blank'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



